.loader,
.loader:before,
.loader:after {
  background: #00ba98;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: #00ba98;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: "";
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

@font-face {
  font-family: "Blogger Sans"; /* Гарнитура шрифта */
  src: url("./fonts/BloggerSans.ttf"); /* Путь к файлу со шрифтом */
}

body,
html {
  font-family: "Blogger Sans";
}

header.header-mental {
  width: 100%;
  height: 75px;
  background: linear-gradient(to left, #cbf25b, #00ba98);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-box-shadow: 0 0 10px rgb(141, 141, 141);
  box-shadow: 0 0 10px rgb(141, 141, 141);
}

.container-mental {
  width: 100%;
  height: calc(100% - 75px);
  position: absolute;
  top: 75px;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-mental-mental > img {
  position: absolute;
  left: 30px;
  top: calc(50% - 100px);
  transition-duration: 0.3s;
  opacity: 0;
}

.left-side-mental {
  width: 320px;
  height: 500px;
  position: absolute;
  top: calc(50% - 250px);
  left: -300px;
  display: flex;
  flex-direction: row;
  align-items: center;
  transition-duration: 0.3s;
}

.left-side-mental > .content {
  width: 300px;
  height: 100%;
  min-height: 100%;
  overflow-y: scroll;
  scrollbar-color: #ccc #fff;
  z-index: 5;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  -webkit-box-shadow: 0 0 10px #ccc;
  box-shadow: 0 0 10px #ccc;
}

.left-side-mental > .content > div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  width: 90%;
  height: 10%;
  min-height: 10%;
  border-bottom: 1px solid rgb(230, 230, 230);
  background-color: #fff;
}

.left-side-mental > .content > div > span > label {
  font-size: 16px;
  font-weight: bold;
}

.left-side-mental > .content > div > span > label:first-child {
  margin-right: 5px;
}

.left-side-mental > .button {
  width: 20px;
  height: 50px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  cursor: pointer;
  -webkit-box-shadow: 0 0 10px #ccc;
  box-shadow: 0 0 10px #ccc;
}

.side-bar-header > .close-btn {
  display: none;
  position: absolute;
  right: 20px;
  color: #fff;
  font-weight: bold;
  font-size: 25px;
  top: calc(37.5px - 12.5px);
}

.save-btn {
  width: 80%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background-color: #00ba98;
  color: #fff;
  margin-top: 10px;
  cursor: pointer;
  transition-duration: 0.3s;
  font-size: 16px;
  font-weight: bold;
  -webkit-box-shadow: 0 0 10px #ccc;
  box-shadow: 0 0 10px #ccc;
}

.answer {
  width: 300px;
  height: 200px;
  border-radius: 10px;
  background-color: #fff;
  -webkit-box-shadow: 0 0 10px #ccc;
  box-shadow: 0 0 10px #ccc;
}

.answer > form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.answer > form > input {
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ccc;
  font-family: "Blogger Sans";
  font-size: 16px;
  font-weight: 500;
  padding-left: 10px;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 5px #ccc;
  box-shadow: 0 0 5px #ccc;
}

.answer > form > input::placeholder {
  color: #ccc;
  font-weight: 600;
}

.answer > form > button {
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  border: 0;
  border-radius: 10px;
  font-size: 16px;
  font-family: "Blogger Sans";
  cursor: pointer;
  transition-duration: 0.3s;
  -webkit-box-shadow: 0 0 5px #ccc;
  box-shadow: 0 0 5px #ccc;
}

.answer > form > button:hover {
  font-size: 19px;
}

.answer > form > .ready {
  background-color: #00ba98;
}

.answer > form > .again {
  background-color: #ffab4a;
}

.save-btn:hover {
  font-size: 20px;
}

.start-btn {
  width: 350px;
  height: 100px;
  background-color: #00ba98;
  color: #fff;
  border: 0;
  cursor: pointer;
  font-weight: 600;
  font-size: 35px;
  border-radius: 10px;
  transition-duration: 0.3s;
  -webkit-box-shadow: 0 0 10px #ccc;
  box-shadow: 0 0 10px #ccc;
}

.start-btn > label {
  transition-delay: 0.1s;
  transition-duration: 0.3s;
  font-family: "Blogger Sans";
  cursor: pointer;
}

.start-btn:hover {
  width: 400px;
  height: 150px;
  font-size: 35px;
}

.count > label {
  font-size: 100px;
  font-weight: 400;
  font-family: "Blogger Sans";
}

.back-count > label {
  font-size: 100px;
  font-weight: bold;
}

.back-item {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-left: 30px;
  cursor: pointer;
}

.back-item > img {
  transition-duration: 0.3s;
}

.back-item > img:hover {
  width: 35px;
  height: 35px;
}

.status-text {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: bold;
  font-family: "Blogger Sans";
  margin-left: 100px;
}

.action-icons {
  width: 350px;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  height: 100%;
  align-items: center;
}

.abacus-item {
  margin-top: 5px;
  cursor: pointer;
}

.signal-item {
  color: #fff;
  font-size: 25px;
  cursor: pointer;
  margin-top: 5px;
  text-shadow: 0 0 5px rgb(119, 119, 119);
  transition-duration: 0.3s;
}

.signal-item:hover {
  font-size: 30px;
}

.settings-item {
  text-shadow: 0 0 5px rgb(119, 119, 119);
  color: #fff;
  font-size: 30px;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 30px;
  cursor: pointer;
  margin-top: 5px;
  transition-duration: 0.3s;
}

.settings-item:hover {
  font-size: 35px;
}

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  z-index: 2; /* Sit on top */
  position: relative;
  left: 0;
  top: 0;
  border-radius: 0;
  width: 100%;
  height: 100%;
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  width: 50%;
  margin: 20px;
  /* height: 0%; */
  min-height: 90%;
  margin: 10% auto;
  border-radius: 10px;
  padding: 10px;
  /* display: flex;
  flex-direction: row;
  justify-content: space-evenly; */
}

.modal-content > .content {
  width: 100%;
  height: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.modal-content > .content > div {
  width: 40%;
  height: 100%;
  /* margin-top: 20px; */
}

.modal-content > h3 {
  font-family: "Blogger Sans";
  font-size: 20px;
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

/* SIDEBAR */

.side-bar-mental {
  z-index: 5;
  width: 400px;
  overflow-y: auto;
  min-height: 100%;
  height: 100%;
  position: absolute;
  right: -500px;
  top: 0;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  transition-duration: 0.3s;
  -webkit-box-shadow: 0 0 10px #ccc;
  box-shadow: 0 0 10px #ccc;
}

.side-bar-header {
  background: linear-gradient(to right, #cbf25b, #00ba98);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 75px;
}

.side-bar-header > label {
  font-size: 25px;
  font-weight: bold;
  color: #fff;
}

.side-bar-mental > .container-side-bar {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  justify-content: space-evenly;
}

.pm-item {
  margin-top: 10px;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.pm-item > label.title {
  font-size: 19px;
  font-weight: 300;
  margin-bottom: 5px;
}

.pm {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  border-radius: 10px;
  border: 1px solid #ccc;
  -webkit-box-shadow: 0 0 5px #ccc;
  box-shadow: 0 0 5px #ccc;
}

.pm > .minus {
  width: 50px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffab4a;
  border-radius: 10px;
  cursor: pointer;
}

.pm > .content {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.pm > .content > input {
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 16px;
}

.pm > .plus {
  width: 50px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #02a744;
  border-radius: 10px;
  cursor: pointer;
}

.d-list-item {
  margin-top: 10px;
  width: 80%;
  display: flex;
  flex-direction: column;
}

.d-list-item > label.title {
  font-size: 19px;
  font-weight: 300;
  margin-bottom: 5px;
}

.d-list-item > select {
  width: 100%;
  height: 50px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #fff;
  padding: 0 5px;
  cursor: pointer;
  position: relative;
}

.d-list-item > select:after {
  right: 11px;
  /*Adjust for position however you want*/

  top: 18px;
  padding: 0 0 2px;
  border-bottom: 1px solid #999;
  /*left line */

  position: absolute;
  pointer-events: none;
}

.d-list-item > select::-ms-expand {
  display: none;
}

/** Adapting fro small devices */

@media only screen and (max-width: 768px) {
  .side-bar-header > .close-btn {
    display: block;
  }

  .back-item > img {
    width: 20px;
    height: 20px;
  }

  .back-item {
    margin-left: 10px;
  }

  .signal-item {
    font-size: 23px;
  }

  .signal-item:hover {
    font-size: 23px;
  }

  .settings-item {
    font-size: 23px;
    margin-right: 20px;
  }

  .settings-item:hover {
    font-size: 23px;
  }

  .abacus-item > img {
    width: 20px;
    height: 20px;
  }

  .start-btn {
    z-index: 100;
  }

  /* .container-mental>img {
    position: absolute;
    left: 50px;
    bottom: 30px;
  } */

  .back-count > label {
    font-size: 30px;
  }

  .modal-content {
    width: 90%;
  }
}
