/* IMPORTS */

@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap");

/* IMPORTS */

/**
 * Basic
 */

* {
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.sidebar {
  width: 60px;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 6;
  background-color: #47415c;
  display: flex;
  flex-direction: row;
  box-shadow: 4px 0px 4px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 4px 0px 4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 4px 0px 4px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 4px 0px 4px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 4px 0px 4px rgba(0, 0, 0, 0.25);
}

.buttons {
  width: 60px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #4d4662;
  filter: drop-shadow(2px 0px 4px rgba(0, 0, 0, 0.25));
}

.parts {
  width: 240px;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding-left: 20px;
}

.parts > div > ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.parts > div > ul > li {
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #796f9c;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  cursor: pointer;
}

.parts > div > ul > li > img {
  margin-right: 20px;
  filter: invert(47%) sepia(13%) saturate(1001%) hue-rotate(213deg)
    brightness(93%) contrast(86%);
}

.parts > div > h2 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 25px;
  line-height: 30px;

  color: #ffffff;
  margin: 20px 0;
}

.buttons > div {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.3s;
}

.buttons > div.item:hover {
  background-color: #645a81;
}

.buttons > div.item > img {
  filter: invert(47%) sepia(13%) saturate(1001%) hue-rotate(213deg)
    brightness(93%) contrast(86%);
}

header {
  width: calc(100% - 60px);
  height: 60px;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  position: absolute;
  left: 60px;
  top: 0;
  z-index: 5;
  filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.25));
}

header > .right {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.coins {
  color: #58b78c;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.coins > img {
  margin-right: 5px;
}

.level {
  color: #e33a5a;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.level > img {
  filter: invert(33%) sepia(97%) saturate(3547%) hue-rotate(331deg)
    brightness(95%) contrast(86%);
  margin-right: 5px;
}

.balance {
  height: 100%;
  width: 120px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.balance > .content {
  height: 70%;
  width: 100%;
  background-color: #f1f1f1;
  border-radius: 0px 0px 5px 5px;
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  justify-content: center;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  border: 1px solid #cccccc;
  margin-right: 20px;
}

.notifications {
  margin-right: 20px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.notifications > img {
  filter: invert(33%) sepia(97%) saturate(3547%) hue-rotate(331deg)
    brightness(95%) contrast(86%);
}

.profile {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background-color: #c4c4c4;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  overflow: hidden;
}

.profile > img {
  border-radius: 20px;
  border: 0;
}

.container-student {
  width: calc(100% - 60px);
  height: calc(100% - 60px);
  position: absolute;
  left: 60px;
  top: 60px;
  overflow-y: scroll;
  background-color: #f1f1f1;
}

.container-student > div {
  width: 100%;
  height: 100%;
}

.trainers {
  padding-left: 80px;
  padding-top: 30px;
}

.trainers > .content {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: auto auto auto;
}

.trainers > .content > .item {
  width: 200px;
  height: 275px;
  background: #ffffff;
  border-radius: 10px;
  margin-right: 30px;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
}

.notification-list {
  width: 350px;
  background-color: #fff;
  color: #000;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  right: 50px;
  top: 60px;
  list-style: none;
  border-radius: 5px;
  padding: 0;
  margin: 0;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
}

.notification-list > li {
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  overflow: hidden;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
  transition-duration: 0.3s;
}

.notification-list > li:hover {
  background-color: #f1f1f1;
}

.notification-list > li > label {
  font-size: 14px;
  width: 200px;
  height: 90%;
  max-height: 90%;
  cursor: pointer;
  text-overflow: ellipsis;
}

.notification-list > li > small {
  cursor: pointer;
  font-size: 11px;
}

.trainers > .content > .item > .image {
  width: 100%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.trainers > .content > .item > .info {
  width: 100%;
  height: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.trainers > .content > .item > .info > button {
  width: 150px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 19px;
  text-align: center;
  cursor: pointer;

  color: #ffffff;
  border: 0;

  background: #e33a5a;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}

.trainers > .content > .item > .info > small {
  text-align: center;
}

small {
  color: #ccc;
  font-size: 12px;
  font-weight: 500;
}

.trainers > .content > .item > .info > h3 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 17px;

  text-align: center;
}

.quests {
  margin-top: -5px;
}

.quests > .head {
  width: 100%;
  height: 30px;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 50px;
  color: #cccccc;
}

.quests > .head > .tables-name > label {
  margin-right: 20px;
}

.quests > ul {
  padding: 0;
  margin: 0;
  width: 100%;
  background-color: #ffffff;
}

.quests > ul > li {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  border-bottom: 1px solid #ccc;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
}

.quests > ul > li > .content {
  display: flex;
  height: 100%;
  align-items: center;
}

.quests > ul > li > .content > img {
  margin-right: 20px;
}

.quests > ul > li > .information {
  height: 100%;
  display: flex;
  align-items: center;
}

.quests > ul > li > .information > div {
  margin-right: 40px;
}

.quests > ul > li > .information > div:last-child {
  margin-right: 20px;
}

.crm {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

.crm > .head {
  width: 100%;
  height: 75px;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  z-index: 3;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.crm > .head > .title > h3 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
}

.crm > .head > .panel {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.crm > .head > .panel > .btn {
  width: 30px;
  height: 30px;
  border-radius: 5px;
  background-color: #e33a5a;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  margin-right: 10px;
  font-weight: bolder;
  cursor: pointer;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
}

.crm > .calendar {
  width: 100%;
  height: calc(100% - 75px);
  max-height: calc(100% - 75px);
  max-width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  overflow: hidden;
}

.crm > .calendar > .item {
  width: 80px;
  height: 80px;
  flex-grow: 1;
  flex-basis: 80px;
  box-sizing: border-box;
  padding: 5px;
  border-right: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  display: flex;
  justify-content: center;
}

.crm > .calendar > .item > .block {
  width: 99%;
  height: 99%;
  background-color: #e33a5a;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 5px 0;
  border-radius: 5px;
  margin-top: 0.5%;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  text-align: center;
  cursor: pointer;
  filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.25));
}

.crm > .calendar > .item > .block * {
  cursor: pointer;
}

.chat {
  display: flex;
  align-items: center;
  justify-content: center;
}

.chat-box {
  width: calc(100% - 200px);
  height: calc(100% - 100px);
  background-color: #f1f1f1;
  border: 1px solid #cccccc;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.chat-box > .chats {
  width: 300px;
  height: 100%;
  border-right: 1px solid #ccc;
  background-color: #ffffff;
}

.chat-box > .chats > .search {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  border-bottom: 1px solid #ccc;
  background-color: #ffffff;
}

.chat-box > .chats > .search > input {
  border: 0;
  height: 100%;
  width: 80%;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  display: flex;
  align-items: center;
  padding: 0 5px;
}

.add-btn {
  cursor: pointer;
}

.more-btn {
  cursor: pointer;
}

.chat-box > .chats > .search > input:focus {
  outline: 0;
  border: 0;
}

.chat-box > .chats > .search > input::placeholder {
  color: #ccc;
}

.chat-box > .chats > ul {
  width: 100%;
  height: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
}

.chat-box > .messaging {
  width: calc(100% - 250px);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.chat-box > .messaging > .head {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  padding: 0 15px;
  color: #4d4662;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  border-bottom: 1px solid #ccc;
}

.chat-box > .messaging > .head > div {
  display: flex;
  align-items: center;
  height: 100%;
}

.chat-box > .messaging > .head > div > img {
  margin-right: 10px;
}

.chat-item {
  width: 100%;
  cursor: pointer;
  height: 100px;
  border-bottom: 1px solid #ccc;
  display: flex;
  align-items: center;
  padding: 0 10px;
  justify-content: space-between;
  transition-duration: 0.3s;
}

.chat-item:hover {
  background-color: #f1f1f1;
}

.chat-item > .img {
  border-radius: 30px;
  display: flex;
  justify-content: center;
  background-color: #ccc;
  align-items: center;
  overflow: hidden;
}

.chat-item > .name-last-message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
}

.chat-item > .name-last-message > label {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  margin-bottom: 10px;
}

.chat-item > .name-last-message > small {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.chat-item > .time-new-message {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
}

.new-message {
  margin-top: 20px;
  align-self: flex-end;
  background-color: #e33a5a;
  border-radius: 10px;
  width: 20px;
  height: 20px;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.input-message {
  position: absolute;
  width: 355px;
  height: 40px;
  z-index: 2;
  left: calc(50% - (355px / 2) + 150px);
  bottom: 65px;
  display: flex;
  align-items: center;
}

.input-message > .input {
  width: 300px;
  background-color: #ffffff;
  border-radius: 5px;
  margin-right: 15px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  border: 1px solid #ccc;
  filter: drop-shadow(0px 1px 5px rgba(0, 0, 0, 0.25));
}

.input-message > .input > input {
  height: 100%;
  width: 90%;
  display: flex;
  align-items: center;
  padding-left: 5px;
  outline: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  border: 0;
}

.input-message > .input > input:focus {
  outline: 0;
  border: 0;
}

.input-message > .input > img {
  cursor: pointer;
}

.input-message > .send-btn {
  cursor: pointer;
  background-color: #e33a5a;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  filter: drop-shadow(0px 1px 5px rgba(0, 0, 0, 0.25));
}

.messages {
  flex: 1;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  padding: 0 10px;
  padding-top: 10px;
}

.messages > li {
  display: flex;
  margin-bottom: 10px;
  flex-direction: column;
  background-color: #47415c;
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.25));
}

.messages > li > label {
  margin-bottom: 5px;
}

.messages > li > small {
  font-size: 12px;
  /* color: #f1f1f1; */
}

.clip-items {
  opacity: 0;
  display: flex;
  width: 200px;
  flex-direction: column;
  background-color: #ffffff;
  border-radius: 5px;
  position: absolute;
  margin: 0 0 130px 0;
  padding: 0;
  list-style: none;
  border: 1px solid #ccc;
  transition-duration: 0.3s;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
}

.input-message > ul > li {
  width: 100%;
  height: 30px;
  border-bottom: 1px solid #ccc;
  display: flex;
  align-items: center;
  padding: 0 5px;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
  transition-duration: 0.3s;
}

.input-message > ul > li:hover {
  background-color: #f1f1f1;
}

.input-message > ul > li > img {
  margin-right: 5px;
}
