.white {
  filter: brightness(0) invert(1);
}

.opacity {
  filter: brightness(0) invert(1) opacity(50%);
}

.rotate180 {
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.rotate90 {
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #f1f1f1;
  overflow: hidden;
}

/**
 * Header 
 */

header.header-teacher {
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #fff;
  justify-content: space-between;
  z-index: 5;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-box-shadow: 0 0 10px #ccc;
  -ms-box-shadow: 0 0 10px #ccc;
  -o-box-shadow: 0 0 10px #ccc;
  box-shadow: 0 0 10px #ccc;
}

header > .left {
  padding-left: 10px;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

header > .right {
  display: flex;
  flex-direction: row;
  height: 80%;
}

header > .right > span {
  border-right: 1px solid #ccc;
  height: 100%;
  margin-right: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-right: 15px;
}

header > .right > span:last-child {
  margin-right: 0;
  border-right: 0;
}

header > .right > span > img.profile {
  filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.25));
  border-radius: 25px;
}

.pink {
  background-color: #ffc1c1;
}

.pink > img {
  filter: invert(56%) sepia(95%) saturate(7106%) hue-rotate(343deg)
    brightness(86%) contrast(107%);
}

.gray {
  background-color: #eee;
}

.circle {
  border-radius: 50%;
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.25));
  margin-right: 10px;
  cursor: pointer;
}

.two-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 10px;
}

.two-center > label:first-child {
  font-weight: 600;
  font-size: 14px;
}

.two-center > label:last-child {
  font-weight: 500;
  font-size: 12px;
  color: #ccc;
}

.new-notification {
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: #e31e24;
  position: absolute;
  top: 2px;
  right: 2px;
}

/**
 * Side Bar
 */

.side-bar {
  width: 250px;
  height: calc(100% - 60px);
  position: absolute;
  left: 0;
  top: 60px;
  background: linear-gradient(to bottom, #00b796, #77df48);
  color: rgba(255, 255, 255, 0.85);
  font-weight: 600;
  font-size: 12px;
}

.side-bar > .title {
  margin-left: 15px;
  margin-top: 10px;
  font-size: 14px;
}

.side-bar > .level1-ul {
  margin-left: 15%;
}

.title {
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

ul > li {
  display: flex;
  flex-direction: row;
  align-items: center;
}

ul > li.block-item {
  width: 70%;
  height: 35px;
  padding-left: 35px;
  margin-left: 15px;
  border-radius: 10px;
  cursor: pointer;
  transition-duration: 0.3s;
}

ul > li.block-item:hover {
  background-color: rgba(255, 255, 255, 0.25);
  color: #fff;
}

ul > li.single {
  margin-bottom: 5px;
  cursor: pointer;
  height: 35px;
  width: 150px;
  padding-left: 5%;
  border-radius: 10px;
  transition-duration: 0.3s;
}

ul > li.single:hover {
  background-color: rgba(255, 255, 255, 0.25);
  color: #fff;
}

ul > li.block-li {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0;
  cursor: default;
  height: auto;
  width: 200px;
  padding-left: 0;
  border-radius: 0;
}

ul > li.block-li > .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: auto;
  margin-bottom: 10px;
  cursor: pointer;
}

ul > li.block-li > .header > span {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 100%;
}

ul > li.block-li > .block > ul {
  width: 180px;
  border-left: 2px solid rgba(255, 255, 255, 0.5);
}

ul > li.block-li > .block {
  margin-bottom: 15px;
}

ul > li.selected {
  background-color: rgba(255, 255, 255, 0.25);
  color: #fff;
}

.side-bar img {
  margin-right: 15px;
}

/**
 * Container
 */

.container-teacher {
  width: calc(100% - 250px);
  min-height: calc(100% - 60px);
  height: calc(100% - 60px);
  position: absolute;
  top: 60px;
  left: 250px;
  overflow-x: hidden;
  overflow-y: scroll;
}

::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: #d3cccc;
  border-radius: 10px;
  max-height: 5px;
}

.header-title {
