  -moz-box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
}

[data-title]::after {
  content: attr(data-title);
  z-index: 100;
  background: #222;
  color: #fff;
  text-align: center;
  width: auto;
  min-width: auto;
  min-height: 35px;
  height: 35px;
  border-radius: 5px;
  font-size: 12px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  pointer-events: none;
  /* left: -10em; top: 0; */
  padding: 5px 10px;
  white-space: nowrap;
  opacity: 0;
  transition-duration: 0.3s;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  -ms-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  -o-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
}

[data-left]:hover::after {
  opacity: 1;
  margin-left: -175px;
}

[data-right]:hover::after {
  opacity: 1;
  margin-right: -250px;
}

[data-bottom]:hover::after {
  opacity: 1;
  margin-bottom: -85px;
}

[data-top]:hover::after {
  opacity: 1;
  margin-top: -150px;
}

