@font-face {
  font-family: 'Blogger Sans'; /* Гарнитура шрифта */
  src: url('./fonts/BloggerSans.ttf'); /* Путь к файлу со шрифтом */
}

:root {
  --icon-size: 25px;
  --bg-normal: green;
  --font-size: 16px;
  --font-color: white;
}

.loader,
.loader:before,
.loader:after {
  background: #00BA98;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: #00BA98;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

* {
    font-family: 'Blogger Sans', sans-serif;
}

.divide-header {
  width: 100%;
  height: 75px;
  background: linear-gradient(to left, #CBF25B, #00BA98);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  -webkit-box-shadow: 0 0 10px rgb(141, 141, 141);
          box-shadow: 0 0 10px rgb(141, 141, 141);
}

.container {
  width: 100%;
  height: calc(100% - 75px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.side-bar-header>.close-btn {
  display: none;
  position: absolute;
  right: 20px;
  color: #fff;
  font-weight: bold;
  font-size: 25px;
  top: calc(37.5px - 12.5px);
}

.save-btn {
  width: 80%;
  height: 50px;
  min-height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background-color: #00BA98;
  color: #fff;
  margin-top: 10px;
  cursor: pointer;
  transition-duration: .3s;
  font-size: 16px;
  font-weight: bold;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
}

.answer {
  width: 300px;
  height: 250px;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
}

.answer>form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.answer>form>input {
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ccc;
  font-family: 'Blogger Sans';
  font-size: 16px;
  font-weight: 500;
  padding-left: 10px;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 5px #ccc;
          box-shadow: 0 0 5px #ccc;
}

.answer>form>input::placeholder {
  color: #ccc;
  font-weight: 600;
}

.answer>form>button {
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  border: 0;
  border-radius: 10px;
  font-size: 16px;
  font-family: 'Blogger Sans';
  cursor: pointer;
  transition-duration: .3s;
  -webkit-box-shadow: 0 0 5px #ccc;
          box-shadow: 0 0 5px #ccc;
}

.answer>form>button:hover {
  font-size: 19px;
}

.answer>form>#ready {
  background-color: #00BA98;
}

.answer>form>#again {
  background-color: #ffab4a;
}

.save-btn:hover {
  font-size: 20px;
}

.start-btn {
  width: 350px;
  height: 100px;
  background-color: #00BA98;
  color: #fff;
  border: 0;
  cursor: pointer;
  font-weight: 600;
  font-size: 35px;
  border-radius: 10px;
  transition-duration: .3s;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
}

.start-btn>label {
  transition-delay: .1s;
  transition-duration: .3s;
  font-family: 'Blogger Sans';
  cursor: pointer;
}

.start-btn:hover {
  width: 400px;
  height: 150px;
  font-size: 35px;
}

.count>label {
  font-size: 100px;
  font-weight: 400;
  font-family: 'Blogger Sans';
}

.back-count>label {
  font-size: 100px;
  font-weight: bold;
}

.back-item {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-left: 30px;
  cursor: pointer;
}

.back-item>img {
  transition-duration: .3s;
}

.back-item>img:hover {
  width: 35px;
  height: 35px;
}

.status-text {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: bold;
  font-family: 'Blogger Sans';
  margin-left: 100px;
}

.action-icons {
  width: 175px;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  height: 100%;
  align-items: center;
}

.abacus-item {
  margin-top: 5px;
  cursor: pointer;
}

.signal-item {
  color: #fff;
  font-size: 25px;
  cursor: pointer;
  margin-top: 5px;
  text-shadow: 0 0 5px rgb(119, 119, 119);
  transition-duration: .3s;
}

.signal-item:hover {
  font-size: 30px;
}

.settings-item {
  text-shadow: 0 0 5px rgb(119, 119, 119);
  color: #fff;
  font-size: 30px;
  height: 100%;
  display: flex;
  flex-direction: row; 
  align-items: center;
  margin-right: 30px;
  cursor: pointer;
  margin-top: 5px;
  transition-duration: .3s;
}

.settings-item:hover {
  font-size: 35px;
}

.more-item {
  width: 90%;
  margin-top: 10px;
}

.more-item>.items,
.hidden>.first,
.hidden>.second {
  width: 100%;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: auto auto auto;
}

.more-item>.items>.item,
.hidden>.first>.f-item,
.hidden>.second>.s-item {
  width: 75px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to right, #00BA98, #01d1ab);
  color: #fff;
  border-radius: 10px;
  font-family: 'Blogger Sans';
  font-weight: bold;
  font-size: 14px;
  transition-duration: .3s;
  cursor: pointer;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
}

.more-item>.items>.item:hover,
.more-item>.items>.item:focus,
.hidden>.first>.f-item:hover,
.hidden>.first>.f-item:focus,
.hidden>.second>.s-item:hover,
.hidden>.second>.s-item:focus {
  font-size: 16px;
}

.hidden {
  display: none;
  width: 90%;
  margin-top: 20px;
}

.hidden>div {
  margin-bottom: 20px;
}

.side-bar-header>label {
  font-size: 25px;
  color: #fff;
  font-weight: bold;
}

.switch-element {
  width: 90%;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin-right: 10px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .2s;
  transition: .2s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .2s;
  transition: .2s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.modal {
  width: 200px;
  height: 100px;
  border-radius: 10px;
  background-color: #fff;
  position: absolute;
  top: -100px;
  left: calc(50% - 100px);
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: .1s;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
}

.left-side-divide {
  width: 320px;
  height: 400px;
  position: absolute;
  top: calc(50% - 250px);
  left: -300px;
  display: flex;
  flex-direction: row;
  align-items: center;
  transition-duration: .3s;
}

.left-side-divide>.content {
  width: 300px;
  height: 100%;
  min-height: 100%;
  overflow-y: scroll;
  z-index: 5;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
}

.left-side-divide>.content>div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  width: 90%;
  height: 10%;
  min-height: 10%;
  border-bottom: 1px solid rgb(230, 230, 230);
  background-color: #fff;
}

.left-side-divide>.content>div>span>label {
  font-size: 16px;
  font-weight: bold;
}

.left-side-divide>.content>div>span>label:first-child {
  margin-right: 5px;
}

.left-side-divide>.button {
  width: 20px;
  height: 50px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  cursor: pointer;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
}

/* SIDEBAR */

.side-bar-divide {
  width: 400px;
  min-height: 100%;
  height: 100%;
  overflow-y: scroll;
  position: absolute;
  right: -500px;
  top: 0;
  background-color: #fff;
  transition-duration: .3s;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
}

.side-bar-header {
  background: linear-gradient(to right, #CBF25B, #00BA98);
  display: flex;
  justify-content: center;
  align-items: center;
}

.side-bar-divide>.container-side-bar {
  display: flex;
  width: 100%;
  min-height: calc(100% - 75px);
  height: calc(100% - 75px);
  flex-direction: column;
  align-items: center;
}

.pm-item {
  margin-top: 10px;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.pm-item>label.title {
  font-size: 19px;
  font-weight: 300;
  margin-bottom: 5px;
}

.pm {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  border-radius: 10px;
  border: 1px solid #ccc;
  -webkit-box-shadow: 0 0 5px #ccc;
          box-shadow: 0 0 5px #ccc;
}

.pm>.minus {
  width: 50px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffab4a;
  border-radius: 10px;
  cursor: pointer;
}

.pm>.content {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.pm>.content>input {
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 16px;
}

.pm>.plus {
  width: 50px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #02A744;
  border-radius: 10px;
  cursor: pointer;
}

/** Adapting fro small devices */

@media only screen and (max-width: 768px) {
  .side-bar-header>.close-btn {
    display: block;
  }

  .back-item>img {
    width: 20px;
    height: 20px;
  }

  .back-item {
    margin-left: 10px;
  }

  .action-icons {
    width: 125px;
  }
  
  .signal-item {
    font-size: 23px;
  }
  
  .signal-item:hover {
    font-size: 23px;
  }
  
  .settings-item {
    font-size: 23px;
    margin-right: 20px;
  }
  
  .settings-item:hover {
    font-size: 23px;
  }
  
  .abacus-item>img {
    width: 20px;
    height: 20px;
  }

  .start-btn {
    z-index: 100;
  }

  .container>img {
    top: auto;
    bottom: 30px;
  }

  .back-count>label {
    font-size: 50px;
  }

  .modal-content {
    width: 90%;
  }
}