#cells {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 75px;
  border-radius: 5px;
  border: 1px solid #ddd;
  box-shadow: 0 0 5px #ccc;
  overflow: hidden;
}

#cells > li {
  width: 100%;
  height: calc(100% / 2);
  border-bottom: 1px solid #ddd;
  display: flex;
  align-items: center;
  justify-content: center;
}

.answer-cells {
  width: 300px;
  min-height: 250px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background: #fff;
  box-shadow: 0 0 5px #ddd;
  border: 1px solid #ddd;
  border-radius: 10px;
}

.answer-cells > input {
  margin: 15px 0 15px calc(50% - 200px / 2);
  width: 200px;
  height: 50px;
  border-radius: 10px;
  border: 1px solid #ccc;
  background-color: #fff;
  box-shadow: 0 0 4px #ccc;
  padding-left: 5px;
  font-weight: 600;
  font-size: 14px;
  outline: 0;
}

.answer-cells > button {
  margin: 0 0 0 calc(50% - 200px / 2);
  width: 200px;
  height: 50px;
  background-color: #ffab4a;
  color: #fff;
  font-weight: 500;
  border-radius: 10px;
  border: 0;
  outline: 0;
  box-shadow: 0 0 5px #ccc;
  cursor: pointer;
  transition: 0.05s;
}

.answer-cells > button:hover {
  font-size: 16px;
}
