/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  z-index: 2; /* Sit on top */
  position: relative;
  left: 0;
  top: 0;
  border-radius: 0;
  width: 100%;
  height: 100%;
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  width: 50%;
  margin: 20px;
  /* height: 0%; */
  min-height: 90%;
  margin: 10% auto;
  border-radius: 10px;
  padding: 10px;
  /* display: flex;
  flex-direction: row;
  justify-content: space-evenly; */
}

.modal-content > .content {
  width: 100%;
  height: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.modal-content > .content > div {
  width: 40%;
  height: 100%;
  /* margin-top: 20px; */
}

.modal-content > h3 {
  font-family: "Blogger Sans";
  font-size: 20px;
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

